import React, { useEffect, useState } from "react";
import styled from "styled-components";
import AproposService from "src/modules/Apropos/AproposService";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import TextFR from "src/view/LegalNotice/textFR";
import TextAR from "src/view/LegalNotice/textAR";

export default function Content() {
  const [currentlySelectedLanguage, setCurrentlySelectedLanguage] =
    useState("fr");

  const getAboutText = () => {
    setCurrentlySelectedLanguage(localStorage.getItem("language") ?? "fr");
  };
  useEffect(() => {
    getAboutText();
  }, []);

  return (
    <ContentLayout>
      <>
        {
          {
            fr: (
              <div
                dangerouslySetInnerHTML={{
                  __html: TextFR(),
                }}
              ></div>
            ),
            en: <></>,
            ar: (
              <div
                dangerouslySetInnerHTML={{
                  __html: TextAR(),
                }}
              ></div>
            ),
          }[currentlySelectedLanguage]
        }
      </>
    </ContentLayout>
  );
}

const ContentLayout = styled.section`
  max-width: 100%;
  margin: 2rem auto;

  p {
    font-size: 1rem;
    line-height: 1.5;
    color: var(--violet);
    text-align: justify;
    margin-bottom: 2rem;
  }

  .Skeleton {
    margin-top: 1rem;
  }
`;
